.geeksforgeeks__stats {
    position: relative;
    transition: var(--transition);
    opacity: 50%;
}

.geeksforgeeks__stats img {
    position: absolute;
    top: 20px;
    right: 140px;
    width: 400px;
    object-fit: contain;
}

.geeksforgeeks__stats:hover {
    opacity: 100%;
}